@import "../../inc/vars";
.acro-compare-table-block {
	padding: 78px 10px 50px;
	width: 1440px;
	max-width: 100%;
	margin: 0 auto;
	@media (min-width: $screen-sm){
		padding: 100px 40px 140px;
	}
	h2 {
		@include titleGrad();
		@include pop(400, 30, 1.24);
		@media (min-width: $screen-sm){
			font-size: 36px;
		}
	}
	h3 {
		@include pop(400, 20, 1.36);
		color: #5A6272;
		margin-bottom: 32px;
		@media (min-width: $screen-sm){
			margin-bottom: 18px;
			font-size: 24px;
		}
	}
	&__title {
		text-align: center;
		margin-bottom: 19px;
		@media (min-width: $screen-sm){
			margin-bottom: 64px;
		}
	}
	&__description {
		@include fig(400, 18, 1.24);
		color: #4f4b58;
		margin-top: 8px;
	}
	&__table {
		color: #4F4B58;
		&__items {
			position: relative;
			.table-row-style-2 {
				position: relative;
				&:before {
					@media (min-width: $screen-sm) {
						content: '';
						display: block;
						width: 35%;
						position: absolute;
						left: 30%;
						top: 0;
						height: 100%;
						background: $green;
						z-index: 2;
					}
				}
				&:after {
					@media (min-width: $screen-sm) {
						content: '';
						display: block;
						width: 35%;
						position: absolute;
						right: 0;
						top: 0;
						height: 100%;
						background: #e2daf4;
						z-index: 2;
					}
				}
				.table-inner-row {
					position: relative;
					z-index: 4;
					background: none;
					.table-col {
						@include pop(600, 20, 1);
						background: #e2daf4;
						@media (min-width: $screen-sm){
							font-size: 23px;
						}
						&:first-child {
							color: #fff;
							background: $green !important;
						}
						ul {
							list-style: none;
							li {
								list-style: none;
							}
						}
					}
				}
			}
			&:after {
				content: '';
				display: block;
				position: absolute;
				width: 40%;
				left: 0;
				height: 100%;
				top: 0;
				background-color: rgba(255, 255, 255, 0.8);
				@media (min-width: $screen-sm){
					display: none;
				}
			}
			.table-inner-row {
				.table-col:first-child {
					order: 2;
				}
			}
			@media (min-width: $screen-sm){
				&:before {
					content: '';
					display: block;
					position: absolute;
					width: 35%;
					left: 65%;
					height: 100%;
					top: 0;
					box-shadow: 0px 0.605px 1.21px 0px rgba(162, 162, 167, 0.15), 0px 7.262px 18.156px 0px rgba(94, 102, 116, 0.10), 0px 3.631px 7.262px 0px rgba(81, 92, 114, 0.10);
					border-image: linear-gradient(#27B989, rgba(#27B989, .01)) 10;
					border-style: solid;
					border-width: 1px;
				}
			}
			.table-row {
				display: flex;
				background: #F4F4F4;
				align-items: center;
				@media (min-width: $screen-sm){
					&:nth-child(odd){
						background: #fff;
					}
				}

				.table-col {
					width: 50%;
					ul {
						list-style: disc;
						li {
							list-style: disc;
						}
					}
				}
				& > {
					.table-col {
						width: 60%;
						padding: 8px 20px;
						@include pop(400, 13, 1.36);
						@media (min-width: $screen-sm){
							padding: 24px 20px 24px 60px;
							font-size: 18px;
							width: 70%;
						}
						&:first-child {
							width: 40%;
							position: relative;
							z-index: 1;
							@media (min-width: $screen-sm){
								font-size: 20px;
								font-weight: 500;
								width: 30%;
								padding-left: 20px;
							}
						}
						&:last-child {
							padding: 0;
							.table-col {
								padding: 8px 10px 8px 60px;
								@media (min-width: $screen-sm){
									padding: 24px 20px 24px 60px;
								}
							}
						}
					}
				}
				.table-col--heading {
					width: 30%;
					font-weight: 500;
					@media (min-width: $screen-sm){
						font-size: 32px;
						padding-left: 44px;
					}
				}
				.table-inner-row {
					display: flex;
					.table-col {
						width: 100%;
						padding: 8px 20px;
						line-height: 1.56;
						@media (min-width: $screen-sm){
							padding: 35px 20px 35px 60px;
							width: 50%;
						}
					}
				}
				&.heading-row {
					width: 100%;
					&.sticky-row {
						background: #fff;
						z-index: 3;
						box-shadow: 0 .605px 1.21px 0 rgba(162,162,167,.15),0 7.262px 18.156px 0 rgba(94,102,116,.1),0 3.631px 7.262px 0 rgba(81,92,114,.1);
					}
					.table-inner-row {
						text-align: center;
						font-size: 11px;
						@media (min-width: $screen-sm){
							font-size: 20px;
						}
						img {
							width: 25px;
							@media (min-width: $screen-sm){
								width: 54px;
							}
						}
						.company-name {
							margin-top: 4px;
							@media (min-width: $screen-sm){
								margin-top: 13px;
							}
						}
						.table-col {
							padding-left: 30px;
							display: flex;
							flex-direction: column;
							justify-content: center;
							align-items: center;
							@media (min-width: $screen-sm){
								padding-left: 20px;
							}
						}
					}
				}
				&:not(.heading-row){
					img {
						@media (min-width: $screen-sm){
							display: none;
						}
					}
					.table-inner-row {
						flex-wrap: wrap;
						.table-col {
							position: relative;
							display: flex;
							flex-direction: column;
							justify-content: center;
							img {
								position: absolute;
								left: 20px;
								top: 50%;
								width: 13px;
								margin-top: -6px;
							}
							&:first-child {
								border-top: 1px solid #27B989;
								background: rgba(255, 255, 255, 0.80);
								border-bottom-width: 0;
								box-shadow: 0px 0.605px 1.21px 0px rgba(162, 162, 167, 0.15), 0px 7.262px 18.156px 0px rgba(94, 102, 116, 0.10), 0px 3.631px 7.262px 0px rgba(81, 92, 114, 0.10);
								border-image: linear-gradient(#27B989, rgba(#27B989, .01)) 30;
								//border-right-image: linear-gradient(#27B989, rgba(#27B989, .01)) 180;
								border-left-width: 1px;
								border-right-width: 1px;
								border-left-style: solid;
								border-right-style: solid;
								@media (min-width: $screen-sm){
									border: none;
									background: none;
									box-shadow: none;
								}
							}
						}
					}
				}
			}
			.table-row:nth-child(2){
				.table-inner-row {
					.table-col:first-child {
						//border-top: 1px solid #27B989;
					}
				}
			}
		}
	}
	&_2 {
		.acro-compare-table-block {
			&__title {
				margin-bottom: 108px;
				@media (max-width: $screen-sm){
					margin-bottom: 123px;
				}
				h3 {
					font-size: 18px;
					margin-bottom: 32px;
					@media(max-width: $screen-sm){
						font-size: 24px;
						line-height: 1.24;
						margin-bottom: 16px;
					}
				}
				h2 {
					font-size: 35px;
					line-height: 1.24;
					margin-bottom: 16px;
					@media(min-width: $screen-sm) {
						margin-bottom: 8px;
						@include pop(400, 44, 1.24);
					}
				}
			}
			&__table {
				@media (min-width: $screen-md){
					padding: 0 100px;
				}
				&__items {
					@media (min-width: $screen-sm){
						border: 1px #d1d5db solid;
						.table-col {
							background: none;
						}
						&:before {
							left: 30%;
							background: #fff;
							border: none;
						}
					}

					.table-row {
						background: none;
						position: relative;
						> .table-col {
							&:first-child {
								font-weight: 600;
							}
						}
						@media (min-width: $screen-sm){
							border-bottom: 1px #d1d5db solid;
						}
						.table-inner-row {
							.table-col {
								&:first-child {
									order: -1;
								}
							}
						}
						&.heading-row {
							.table-inner-row {
								.table-col {
									font-weight: 700;
									@media (min-width: $screen-sm){
										font-size: 18px;
										img {
											width: 40px;
											margin-bottom: 30px;
										}
									}
									&:first-child {
										color: $green;
										@media (min-width: $screen-sm) {
											img {
												width: 48px;
											}
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}
}